@import '../components/styles/sass/variables';
@import '../components/styles/sass/mixins';

.mainBlock {
  background-color: #1f1f20;
  min-height: 100vh;
  border: none;
  @include flex(row, center, center, nowrap);

  .container {
    @include container();
    @include flex(row, space-around, center, wrap);

    .text1 {
      width: 300px;
      font-size: 15px;
      font-family: $titleFontFamily;
      color: $textColor;
    }

    .title {
      width: 300px;
      font-size: 45px;
      font-family: $titleFontFamily;
      color: $textColor;
      letter-spacing: 5px;
      line-height: 1.2;
    }

    .text {
      width: 300px;
      font-family: $textFontFamily;
      color: $textColor;
      font-size: 25px;
    }

    .photo {
      width: 300px;
      height: 400px;
      background-image: url("../../src/assets/image/DSC_815131.jpg");
      object-fit: cover
    }

    @media screen and (max-width: 750px) {
      padding-top: 20px;
      padding-bottom: 20px;
    }
  }
}


